/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 1rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: 1fr 55%;
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 25rem;
  background-image: url(../img/1.jpg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .navBar {
  background-color: #FFFFFF;
  overflow: hidden;
  font-weight: 700;
}
.contenedor-app .navBar ul {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  padding: 0.5rem;
  margin: 0;
}
.contenedor-app .navBar ul li {
  text-transform: uppercase;
  background-color: white;
  border-radius: 15px;
  height: 2.5em;
  position: relative;
  overflow: hidden;
  margin-right: 1rem;
  padding: 0.7em;
  cursor: pointer;
}
.contenedor-app .navBar ul li a {
  text-align: center;
  color: white;
  mix-blend-mode: difference;
  padding-left: 0.5rem;
}
.contenedor-app .navBar ul li::before {
  content: "";
  position: absolute;
  width: 1.5em;
  height: inherit;
  background-color: black;
  border-radius: 50%;
  top: 0;
  left: -0.75em;
  transition: 1s ease-out;
}
.contenedor-app .navBar ul li:hover::before {
  transform: scale(15);
}
.contenedor-app .navBar ul li.active {
  background-color: black;
  color: #0da6f3;
}
.contenedor-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 3rem 2rem;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}

.barra {
  margin: 4rem 0;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra span {
  color: #0da6f3;
}
.barra .boton {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
  transition: color 300ms ease;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: black;
  cursor: pointer;
}
.barra .boton:hover {
  background-color: #1a1b15;
  color: #0da6f3;
}

.barra-servicios {
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-around;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
}
.listado-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio p {
  color: #1a1b15;
  margin: 0 0 1rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #0da6f3;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid #e1e1e1;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 4rem;
}

.citas, .servicios {
  list-style: none;
  padding: 2rem;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.citas li p, .servicios li p {
  font-weight: 700;
  color: #0da6f3;
}
.citas li span, .servicios li span,
.citas li .servicio,
.servicios li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}

.servicios .acciones {
  margin: 0;
}
.servicios .boton,
.servicios .boton-eliminar {
  margin: 0;
  border-radius: 1rem;
}

textarea {
  width: 100%;
  resize: none;
}

