.formulario {
  margin: 3rem auto;
  padding: auto;
  width: 70%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo label::after {
  content: " * ";
  color: red;
}
.campo input {
  flex: 1;
  border: none;
  padding: 0.8rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: #6d7158;
  color: #FFFFFF;
  cursor: not-allowed;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}
.boton:hover {
  background: #FFFFFF;
  color: #0da6f3;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
  font-style: italic;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}
.boton-eliminar:hover {
  background: #FFFFFF;
  color: #cb0000;
}

.acciones {
  margin: 4rem 7rem 2rem 7rem;
  padding: 1rem 3rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones span {
  padding-top: 0;
}
.acciones a {
  font-style: italic;
  color: #FFFFFF;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  transition: color 300ms ease;
}
.acciones a:hover {
  background: #FFFFFF;
  color: #0da6f3;
}

.alerta {
  padding: 0.6666666667rem;
  color: #cb0000;
  width: 70%;
  display: block;
  margin: 2rem auto;
  text-align: center;
  font-style: italic;
  border-radius: 0.5rem;
}
.alerta.error {
  background-color: #f3acac;
}
.alerta.exito {
  background-color: #04AA6D;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #0da6f3;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
  border-radius: 0.5rem;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #0a85c3;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #0da6f3;
}

.paginacion {
  padding: 4rem 0;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}